@import url(https://fonts.googleapis.com/css?family=Roboto+Slab);
html {
  font-size: 62.5%; }

body {
  font-family: 'Roboto Slab', sans-serif;
  font-size: 1.6rem;
  background: #1b065e;
  height: 100%;
  color: #fdfffc; }

a {
  color: #fdfffc;
  text-decoration: none; }
  a:hover {
    color: #95c623; }

button {
  cursor: pointer; }

button:disabled {
  cursor: default; }

.is-active {
  font-weight: 500;
  color: #95c623; }

.text-secondary {
  color: #95c623; }

.menu-button {
  position: absolute;
  z-index: 3;
  right: 3.5rem;
  top: 3.5rem;
  cursor: pointer;
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out; }
  .menu-button.close {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg); }
    .menu-button.close .menu-button__line:nth-child(1) {
      -webkit-transform: rotate(45deg) translate(5px, 5px);
              transform: rotate(45deg) translate(5px, 5px); }
    .menu-button.close .menu-button__line:nth-child(2) {
      opacity: 0; }
    .menu-button.close .menu-button__line:nth-child(3) {
      -webkit-transform: rotate(-45deg) translate(7px, -6px);
              transform: rotate(-45deg) translate(7px, -6px); }
  .menu-button .menu-button__line {
    width: 2.8rem;
    height: 0.3rem;
    margin: 0 0 0.5rem 0;
    background: #fffffa;
    -webkit-transition: all 0.5s ease-out;
    transition: all 0.5s ease-out; }

.menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  opacity: 0.9;
  display: flex;
  justify-content: center;
  visibility: hidden; }
  .menu.show {
    visibility: visible; }
  .menu__brand, .menu__navigation {
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    justify-content: center;
    width: 50%;
    height: 100vh;
    overflow: hidden; }
  .menu__navigation {
    margin: 0;
    padding: 0;
    background: #140446;
    list-style: none;
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);
    -webkit-transition: all 0.5s ease-out;
    transition: all 0.5s ease-out; }
    .menu__navigation.show {
      -webkit-transform: translate3d(0, 0, 0);
              transform: translate3d(0, 0, 0); }
  .menu__brand {
    background: #1b065e;
    -webkit-transform: translate3d(0, 100%, 0);
            transform: translate3d(0, 100%, 0);
    -webkit-transition: all 0.5s ease-out;
    transition: all 0.5s ease-out; }
    .menu__brand.show {
      -webkit-transform: translate3d(0, 0, 0);
              transform: translate3d(0, 0, 0); }
    .menu__brand .portrait {
      width: 25rem;
      height: 25rem;
      background: url(/dev-website/static/media/portrait.989d06ac.png);
      border-radius: 50%;
      border: solid 2px #95c623; }
  .menu .menu__navigation__item {
    -webkit-transform: translate3d(600px, 0, 0);
            transform: translate3d(600px, 0, 0);
    font-weight: 400;
    color: #fffffa;
    -webkit-transition: all 0.5s ease-out;
    transition: all 0.5s ease-out; }
    .menu .menu__navigation__item.show {
      -webkit-transform: translate3d(0, 0, 0);
              transform: translate3d(0, 0, 0); }
  .menu .menu__navigation__item__link {
    display: inline-block;
    position: relative;
    font-size: 3.2rem;
    text-transform: uppercase;
    padding: 1.6rem 0;
    text-decoration: none;
    letter-spacing: 0.2rem;
    -webkit-transition: all 0.5s ease-out;
    transition: all 0.5s ease-out; }
    .menu .menu__navigation__item__link:hover {
      color: #95c623; }

.menu__navigation__item:nth-child(1) {
  -webkit-transition-delay: 0.1s;
          transition-delay: 0.1s; }

.menu__navigation__item:nth-child(2) {
  -webkit-transition-delay: 0.2s;
          transition-delay: 0.2s; }

.menu__navigation__item:nth-child(3) {
  -webkit-transition-delay: 0.3s;
          transition-delay: 0.3s; }

.menu__navigation__item:nth-child(4) {
  -webkit-transition-delay: 0.4s;
          transition-delay: 0.4s; }

img {
  width: 8rem;
  height: 8rem;
  position: absolute;
  z-index: 3;
  left: 10rem;
  top: 3.5rem;
  cursor: pointer; }

.icons {
  margin-top: 1rem; }
  .icons a {
    padding: 1rem; }
  .icons a:nth-child(1) {
    padding: 1rem 1rem 1rem 0; }

body {
  background: url(/dev-website/static/media/laptop.0bcc732c.jpg);
  background-attachment: fixed;
  background-size: cover; }
  body:after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background: rgba(27, 6, 94, 0.7); }

main {
  min-height: calc(100vh - 60px);
  padding: 0 10rem;
  display: flex;
  flex-direction: column;
  justify-content: center; }

h1 {
  font-size: 7rem;
  line-height: 7.5rem;
  margin: 0;
  font-weight: 500; }

h2 {
  font-weight: normal; }

